import React from 'react';
import { Row, Col, Input, PageHeader } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { FlowContextMenu } from './components/EditorContextMenu';
import { FlowToolbar } from './components/EditorToolbar';
import { FlowItemPanel } from './components/EditorItemPanel';
import CustomNode from './components/node/nodeTensorflow';
import StartNode from './components/node/nodeStart';

// import '../components/EditorDetailPanel/index.less';
import { withRouter } from 'react-router-dom';

import styles from './index.module.less'

@withRouter
class CreateProcess extends React.Component {

  constructor(props){
    super(props);
    
    this.state = {
      
    }
  }


  componentDidMount(){
  }

  render() {
    return (<div className={styles.canvasContainer}>
        <GGEditor 
          className={styles.editor}
          onBeforecommandexecute={({ command }) => {
            console.log('command', command);
          }}
        >
          <PageHeader
            className={styles.pageheader}
            title="新建流程"
            onBack={() => this.props.history.push('/index/process')}
            subTitle="请拖拽节点创建一个流程"
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            <Col xxl={4} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
            <Input.Search
              placeholder="搜索组件"
              style={{margin: '5px 0', width: '95%', textAlign: 'center', alignSelf: 'center'}}
              enterButton
              size="large"
              onSearch={value => console.log(value)}
            />
              <FlowItemPanel />
            </Col>
            <Col xxl={20} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
              <FlowToolbar />
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
              />
            </Col>
          </Row>
          <FlowContextMenu />
        </GGEditor>
   </div> );
  }
};

export default CreateProcess;
