import React from 'react';
import { Row, Col, Input, PageHeader, Button, Modal, Form, message } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';
import  {connect} from 'react-redux'

import { FlowDetailPanel } from './components/EditorDetailPanel';
import ProcessTree from './components/ProcessTree'
import CustomNode from './components/node/nodeTensorflow';
import StartNode from './components/node/nodeStart';

import { actionCreator } from './store'
import styles from './index.module.less'

import {loadPublicComponentInfo} from '../../api/component'
import {createExperiment} from '../../api/experimets'

@withRouter @Form.create()
class CreateExperiment extends React.Component {

  constructor(props){
    super(props);
    const processObj = this.props.processData && JSON.parse(this.props.processData)
    if(processObj){
      processObj.nodes.map(item=>{
        item.id = item.id.toString()  // 将id转为字符串

        // 初始化实验参数
        if(item.conf){
          console.log('组件', item.label)
          console.log('参数', item.conf)
          for(let key in item.conf){
            console.log(item.label+'_'+key, item.conf[key])
            this.props.saveParamsData(item.label+'_'+key, item.conf[key])
          }
        }
      });
      processObj.edges.map((item,index)=>{
        item.id = (item.id + index).toString();
        item.source = item.source.toString();
        item.target = item.target.toString();
      })
    }
    this.state = {
      processObj
    }
  }

  // componentWillUnmount(){
  // }

  handleSaveExp = () => {  // 保存实验
    console.log(this.state.processObj)
    const { getFieldsValue} = this.props.form
    let params = getFieldsValue();
    let f = true
    for(let i in params){
      if(params[i]===undefined){
        message.warning('请填写完整信息!');
        f=false;
        this.setState({ buttonLoading:false})
        break;
      }
    }
    params = {
      fkWorkflowId: this.props.processId,
      paramJsonString: JSON.stringify(this.props.paramsData),
      ...params
    }
    createExperiment(params).then(res=>{
      if(res){
        if(res.data.meta.code === '001'){
          message.success('实验保存成功')
          this.setState({visible: false})
          this.props.history.push('/index/process')
        }else{
          message.error(res.data.meta.message)
        }
      }
    })
  }

  handleCancel = () => {
    this.setState({visible: false})
  }

  componentDidMount(){
    let componentInfo = {}    // 获取组件的参数配置  
    loadPublicComponentInfo().then(res=>{
      if(res.data.meta.code == '001'){
        res.data.data['System Component'].map(item=>{
          componentInfo = { ...componentInfo, [item.componentId] : item.variables}
        })
        this.props.saveComponentInfo(componentInfo)
      }
    })
  }

  onSaveClick = () => {
    const {paramsData} = this.props
    console.log(paramsData)
    if(paramsData['data_import_inputdata']){
      this.setState({visible: true})
    }else{
      message.warning('请为数据导入组件选择数据集！')
    }
  }

  render() {
    const {getFieldDecorator}=this.props.form;
    const config={
        rules:[{
            required:true,
            message:'Please input!'
        }]
    }

    return (<div className={styles.canvasContainer}>
        <GGEditor className={styles.editor}>
            <PageHeader
              className={styles.pageheader}
              title="创建实验"
              onBack={() => this.props.history.push('/index/process')}
              subTitle="请配置节点参数以创建一个实验"
              extra={[
                <Button type="primary" onClick={this.onSaveClick}>保存实验</Button>,    
              ]}
          />
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            <Col xxl={4} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
              <ProcessTree />
            </Col>
            <Col xxl={14} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                data={this.state.processObj}
              />
            </Col>
            <Col xxl={6} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
              <FlowDetailPanel/>
            </Col>
          </Row>
        </GGEditor>
      <Modal
          title="流程"
          visible={this.state.visible}
          onOk={this.handleSaveExp}
          onCancel={this.handleCancel}
          confirmLoading={this.state.buttonLoading}
        >
          <Form labelCol={{span:6}} wrapperCol={{span:12}}>
            <Form.Item label='实验名称'>
              {
                  getFieldDecorator('name',config)(<Input placeholder='请输入实验名称' />)
              }
            </Form.Item>
            <Form.Item label='实验描述'>
              {
                  getFieldDecorator('experimentDesc',config)(<Input.TextArea placeholder='请输入实验描述' />)
              }
            </Form.Item>
          </Form>
        </Modal>
   </div> );
  }
};

const mapStateToProps = (state) => {
    return {
      processData: state.process.processData,
      processId: state.process.processId,
      paramsData: state.process.paramsData
    }
  }
  
const mapDispatchToProps = (dispatch) => {
    return {
      saveComponentInfo(componentInfo){
        dispatch(actionCreator.saveComponentInfo(componentInfo))
      },
      saveParamsData(name, param){
        dispatch(actionCreator.saveParamsData(name, param))
      }
    }
  }
  
export default connect(mapStateToProps, mapDispatchToProps)(CreateExperiment);

